import HTMLReactParser from "html-react-parser";
import { nanoid } from "nanoid";


type WorkType = {
    title: string,
    link: string,
    description: string[],
    outcome: string
}


const Work = (props: WorkType) => {
    const { title, link, description, outcome } = props;

    return (
        <>
            <br />

            {
                title && (
                    <div className="fw-bold">
                        {
                            link ? (
                                <a href={link} target="_blank" rel="noreferrer">
                                    {HTMLReactParser(title)}
                                </a>
                            ) : (
                                <>
                                    {HTMLReactParser(title)}
                                </>
                            )
                        }
                    </div>
                )
            }

            {
                description && (
                    <ul className="text-justify">
                        {
                            description.map((item: string) => <li key={nanoid()}>{HTMLReactParser(item)}</li>)
                        }
                    </ul>
                )
            }

            {
                outcome && (
                    <p className="text-muted">
                        {HTMLReactParser(outcome)}
                    </p>
                )
            }
        </>
    );
};


const AddWork = (props: {
    work: WorkType[]
}) => {
    const { work } = props;

    return (
        <>
            {
                work.map((data) => {
                    return (
                        <Work
                            key={nanoid()}
                            title={data.title}
                            link={data.link}
                            description={data.description}
                            outcome={data.outcome}
                        />
                    );
                })
            }
        </>
    );
};


export { Work, AddWork };