import { nanoid } from "nanoid";


const Technical = (props: {
    skill: string,
    expertise?: string
}) => {
    const { skill, expertise } = props;

    return (
        <div>
            {
                skill && (
                    <span>
                        {skill}
                    </span>
                )
            }

            {
                expertise && (
                    <span className="text-muted">
                        &nbsp;({expertise})
                    </span>
                )
            }
        </div>
    );
};

const AddSkills = (props: {
    skills: string[]
}) => {
    const { skills } = props;

    return (
        <>
            {
                skills.map((skill) => {
                    return (
                        <Technical
                            key={nanoid()}
                            skill={skill}
                        />
                    );
                })
            }
        </>
    );
};


export { Technical, AddSkills };