import HTMLReactParser from "html-react-parser";

export const Experience = (props: {
    designation: string,
    company: string,
    location: string,
    join: string,
    exit: string
}) => {
    const { designation, company, location, join, exit } = props;

    return (
        <>
            {
                designation && (
                    <div className="d-flex justify-content-between gap-0-2">
                        <div>
                            <div className="fw-bold">
                                {designation}
                            </div>

                            {
                                company && (
                                    <div className="small text-muted">
                                        <span className="fw-bold">
                                            {HTMLReactParser(company)}
                                        </span>

                                        {location && (
                                            <span className="fw-500">
                                                &nbsp;-&nbsp;{location}
                                            </span>
                                        )}
                                    </div>
                                )
                            }
                        </div>

                        {
                            join && exit && (
                                <div>
                                    {join}&nbsp;-&nbsp;{exit}
                                </div>
                            )
                        }
                    </div>
                )
            }
        </>
    );
};

export default Experience;