export const Education = (props: {
    school: string,
    location: string,
    course: string,
    start: string,
    end: string,
    score: string
}) => {
    const { school, location, course, start, end, score } = props;

    return (
        <div className="d-flex justify-content-between gap-0-2">
            <div>

                {
                    course && (
                        <div className="course fw-bold">
                            {course}
                        </div>
                    )
                }

                {
                    school && (
                        <div className="fw-bold text-muted">
                            {school}

                            {
                                location && (
                                    <span className="fw-500">
                                        , {location}
                                    </span>
                                )
                            }
                        </div>
                    )
                }
            </div>

            <div style={{textAlign: "end"}}>
                {
                    start && end && (
                        <div>
                            {start}&nbsp;-&nbsp;{end}
                        </div>
                    )
                }

                {
                    score && (
                        <div>
                            {score}
                        </div>
                    )
                }
            </div>
        </div>
    );
};

export default Education;